.grid {
    margin-right: auto;
    margin-left: auto;
    &__container {
        margin-right: auto;
        margin-left: auto;
        max-width: map-get($breakpoints, lg);
        padding: $reflex-grid-spacing;
        @include max-screen(map-get($breakpoints, lg)) {
            padding: $reflex-grid-spacing;
            max-width: 960px;
        }
    }
    &__col-bleed-left {
        padding-left: 0 !important;
    }
    &__col-bleed-right {
        padding-right: 0 !important;
    }
}