.button {
  cursor: pointer;
  padding: 12px 36px;
  border-radius: 5px;
  margin-bottom: 24px;
  display: inline-block;
  line-height: 1;
  &_primary {
    border: 1px solid $primary;
    color: $primary;
    font-size: 12px;
    font-weight: 400;
    background: $white;
    background: linear-gradient(to bottom, $white 0%, $white 49%, $button-bg 50%);
  }
  &_primary:hover {
    box-shadow: 0 0 2px $primary;
  }
}