.form__lbl {
  position: relative;
  text-indent: 30px;
  margin-bottom: 12px;
  cursor: pointer; }

.form__lbl:before {
  position: absolute;
  top: 0;
  left: 0px;
  display: block;
  content: '';
  width: 20px;
  height: 20px;
  border: 1px solid #000; }

.form__lbl:after {
  position: absolute;
  top: 2px;
  left: -27px;
  display: block;
  visibility: hidden;
  content: "\f00c";
  width: 10px;
  height: 10px;
  font: normal normal normal 14px/1 FontAwesome; }

[type="checkbox"]:checked + .form__lbl:after {
  visibility: visible;
  color: #fff; }

[type="checkbox"]:checked + .form__lbl:before {
  background-color: #00aeef;
  border: none; }

.form__input {
  border: 1px solid #000;
  border-radius: 3px;
  padding: 9px 13px;
  color: #666666;
  font-size: 11px;
  font-weight: 100;
  margin-bottom: 12px; }

.form__input:active, .form__input:focus {
  box-shadow: 0 0 0 2px #000; }

.form__input-wrapper {
  position: relative; }

.form__input-wrapper .form__input {
  position: relative;
  z-index: 1;
  text-indent: 20px; }

.form__input-wrapper:before {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-100%);
  z-index: 2; }

.form__chkbx {
