.menu_top {
  font-size: 12px;
  display: flex;
  align-items: center; }

.menu__list_top {
  display: table;
  width: 100%; }

.menu__list, .menu__list_submenu {
  margin: 0;
  padding: 0; }

.menu__list_submenu {
  text-align: center;
  border: 2px solid #000;
  visibility: hidden;
  background-color: #fff;
  /* hides sub-menu */
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  transform: translateY(-2em);
  z-index: -1;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s; }

.menu__item {
  list-style-type: none; }

.menu__item_top {
  display: table-cell;
  position: relative;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px; }

.menu__item_top:hover {
  cursor: pointer;
  background-color: #414142; }

.menu__item_parent:hover > .menu__list_submenu {
  visibility: visible;
  /* shows sub-menu */
  opacity: 1;
  z-index: 1;
  transform: translateY(0%);
  transition-delay: 0s, 0s, 0.3s;
  /* this removes the transition delay so the menu will be visible while the other styles transition */ }

.menu__link_top {
